@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.banner{position:relative;background:linear-gradient(45deg, #DDEDFB, #D9E7F3)}.banner:before{position:absolute;content:"";width:100%;height:20vw;background:white;left:0;bottom:-15vw;-webkit-transform:skewY(5deg);transform:skewY(5deg);z-index:1}.banner .container{position:relative;display:flex;justify-content:space-around;align-items:center;padding-top:275px;padding-bottom:275px}.banner .content-wrapper{width:35%;text-align:right;margin-left:0;margin-right:auto}.banner .img-wrapper{position:absolute;bottom:0;right:10%;max-width:650px}.banner h1{font-size:70px;line-height:1}.banner h2{font-size:36px}

.about{position:relative;z-index:1}.about .container{display:flex;justify-content:space-between;align-items:flex-start;padding-top:150px}.about .content-wrapper{width:50%}.about .img-wrapper{width:40%}

@-webkit-keyframes modal-video{from{opacity:0}to{opacity:1}}@keyframes modal-video{from{opacity:0}to{opacity:1}}@-webkit-keyframes modal-video-inner{from{-webkit-transform:translate(0, 100px);transform:translate(0, 100px)}to{-webkit-transform:translate(0, 0);transform:translate(0, 0)}}@keyframes modal-video-inner{from{-webkit-transform:translate(0, 100px);transform:translate(0, 100px)}to{-webkit-transform:translate(0, 0);transform:translate(0, 0)}}.modal-video{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5);z-index:1000000;cursor:pointer;opacity:1;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-duration:.3s;animation-duration:.3s;-webkit-animation-name:modal-video;animation-name:modal-video;-webkit-transition:opacity .3s ease-out;transition:opacity .3s ease-out}.modal-video-effect-exit{opacity:0}.modal-video-effect-exit .modal-video-movie-wrap{-webkit-transform:translate(0, 100px);transform:translate(0, 100px)}.modal-video-body{max-width:940px;width:100%;height:100%;margin:0 auto;display:table}.modal-video-inner{display:table-cell;vertical-align:middle;width:100%;height:100%}.modal-video-movie-wrap{width:100%;height:0;position:relative;padding-bottom:56.25%;background-color:#333;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-duration:.3s;animation-duration:.3s;-webkit-animation-name:modal-video-inner;animation-name:modal-video-inner;-webkit-transform:translate(0, 0);transform:translate(0, 0);-webkit-transition:-webkit-transform .3s ease-out;transition:-webkit-transform .3s ease-out;transition:transform .3s ease-out;transition:transform .3s ease-out, -webkit-transform .3s ease-out}.modal-video-movie-wrap iframe{position:absolute;top:0;left:0;width:100%;height:100%}.modal-video-close-btn{position:absolute;z-index:2;top:-35px;right:-35px;display:inline-block;width:35px;height:35px;overflow:hidden;border:none;background:transparent}.modal-video-close-btn:before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.modal-video-close-btn:after{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.modal-video-close-btn:before,.modal-video-close-btn:after{content:'';position:absolute;height:2px;width:100%;top:50%;left:0;margin-top:-1px;background:#fff;border-radius:5px;margin-top:-6px}.video-wrapper{position:relative;width:calc(100% / 3 - 20px);height:15vw;max-height:250px;margin:10px;background-size:cover;background-position:center}.video-wrapper button.open-video{display:flex;justify-content:center;align-items:center;position:absolute;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.2);border:0;outline:0;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.video-wrapper button.open-video:hover,.video-wrapper button.open-video:focus{background:transparent}.video-wrapper button.open-video:hover .play,.video-wrapper button.open-video:focus .play{opacity:1}.video-wrapper .play{width:50px;background:white;border-radius:50%;padding:18px 6px 18px 12px;opacity:0.7;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.video-wrapper .play svg{width:25px}

.videos-group{display:flex;justify-content:flex-start;align-items:flex-start;flex-wrap:wrap}




.media h2{text-align:center}.media-tabs-group{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;padding:0;margin:40px 0}.media-tabs-group li{list-style:none;padding:0}.media-tab{background:transparent;border:1px solid #3A3A3A;font-size:20px;padding:10px 30px 8px;border-radius:2px;font-family:'Bebas Neue', cursive;margin:8px;outline:0;opacity:0.5;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.media-tab:hover:not(.active),.media-tab:focus:not(.active){opacity:1;border:2px solid #3A3A3A;margin:7px}.media-tab.active{background:#3A3A3A;border:2px solid #3A3A3A;color:white;opacity:1}.media-content:not(.active){display:none}


.MuiButton-containedPrimary.custom-submit{border-radius:3px;background:black}.MuiButton-containedPrimary.custom-submit:hover{background:grey}


.center-btn{text-align:center}a.btn{color:inherit}button:hover{cursor:pointer}.btn{display:inline-block;text-decoration:none;padding:15px 35px 13px;margin-top:20px;font-size:22px;text-align:center;outline:0;border-radius:2px;font-family:'Bebas Neue', cursive;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.btn.btn-cta{background:#3A3A3A;color:white;border:0}.btn.btn-cta:hover,.btn.btn-cta:focus{background:#515151;-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2)}.btn.btn-hollow{border:1px solid;background:transparent}.btn.btn-hollow:hover,.btn.btn-hollow:focus{-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2)}

body{font-family:"Lato",sans-serif;color:#1F1F1F}h1,h2,h3,h4,h5{font-family:"Bebas Neue",cursive;margin:0;line-height:1.4}h2{font-size:70px}p,li{font-size:20px;line-height:1.6}img{max-width:100%}.container{padding:80px 8%;max-width:1400px;margin:auto}#about,#media,#contact{outline:0}header{display:none}

